.bigB {
  position: fixed;
  min-width: 100%;
  text-align: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 15rem;
  font-family: "KDA";
  z-index: -1;
  color: white;
  -webkit-text-stroke: 2px #97c6dd;
}

.uniqueTime {
  transition: all 0.2s ease;
  -webkit-animation: flicker-in-1 2s linear both;
  animation: flicker-in-1 2s linear both;
}
.flicker-in-1 {
  transition: all 0.2s ease;
  -webkit-animation: flicker-in-1 5s infinite linear both;
  animation: flicker-in-1 5s infinite linear both;
}

@media screen and (max-width: 1670px) {
  .bigB {
    font-size: 5rem;
  }
}

